<template>
  <view>
    <!--头部导航栏开始-->
    <navigator-bar>
      <view slot="center"><cate @changeType="changeType" /></view>
    </navigator-bar>
    <!--头部导航栏结束-->
    <scroll-view scroll-y="true" class="scrollY">
      <!--关注列表开始-->
      <notice-swiper></notice-swiper>
      <!--关注列表结束-->
      <!--中间的楼层数据开始-->
      <index-floor
        v-for="(item, index) in posts[activeType].list"
        :list="item"
        @bandleMoreBtn="bandleMoreBtn"
        @click.native="forPostDetail(item.articleId, item.tagList)"
        :key="activeType + item.articleId"
        @unlikeIt="unlikeIt"
        @likeIt="likeIt"
        @unNoticeIt="unNoticeIt"
        @noticeIt="noticeIt"
      ></index-floor>
      <!--中间的楼层数据结束-->
      <!-- 上拉加载更多开始 -->
      <view>
        <load-more :loadStatus="loadStatus"></load-more>
      </view>
      <!-- 上拉加载更多结束 -->
    </scroll-view>
    <!-- 点击更多后产生的的上拉菜单开始 -->
    <uni-popup ref="popup" :mask-click="false">
      <popup-bottom @close="close" />
    </uni-popup>
    <!-- 点击更多后产生的的上拉菜单结束 -->
  </view>
</template>

<script>
import LoadMore from "../../../components/content/LoadMore.vue";
import PopupBottom from "../../../components/square/PopupBottom.vue";
import navigatorBar from "../../../components/content/NavigatorBar.vue";
import Cate from "../../../components/content/Cate.vue";
import indexFloor from "../../../components/square/IndexFloor.vue";
import NoticeSwiper from "../../../components/square/NoticeSwiper.vue";
import {
  getPost,
  setLike,
  setUnLike,
  setNoticeIt,
  setUnnoticeIt,
  setPhoto
} from "../../../axios/square/square.js";
export default {
  data() {
    return {
      posts: {
        follow: {
          list: [],
        },
        recommend: {
          list: [],
        },
      },
      activeType: "follow",
      loadStatus: "more", //加载样式：more-加载前样式，loading-加载中样式，nomore-没有数据样式
      isLoadMore: false, //是否加载中
      maxPageNum: 1, //数据库中数据的最大页数(帖子)
      page: { follow: 1, recommend: 1 }, //当前加载的页数（帖子）
      loadSize: 20, //一次加载最多20条（帖子）
    };
  },
  components: {
    LoadMore,
    navigatorBar,
    Cate,
    indexFloor,
    NoticeSwiper,
    PopupBottom,
  },
  methods: {
    //头部导航关注/推荐的切换
    changeType(index) {
      let type = ["follow", "recommend"];
      this.activeType = type[index];
    },
    //点击更多的函数
    bandleMoreBtn() {
      // 通过组件定义的ref调用uni-popup方法 ,如果传入参数 ，type 属性将失效 ，仅支持 ['top','left','bottom','right','center']
      this.$refs.popup.open("bottom");
    },
    //点击获取帖子的详情
    forPostDetail(articleId, tagList) {
      let userDetail = { articleId, tagList };
      uni.navigateTo({
        url:
          "../../level2/square/postDetail?userDetail=" +
          JSON.stringify(userDetail),
      });
    },
    //关闭遮罩
    close() {
      this.$refs.popup.close();
    },
    //获取推荐帖子完整的信息包括用户信息
    getPost(type, uid, page, size, current) {
      return getPost({ uid, page, size, current }).then(
        (res) => {
          //每次请求都要更新当前数据库最大页数
          this.maxPageNum = res.data.data.maxPageNum;
          let postDetailList = [];
          for (let i = 0; i < res.data.data.detail.length; i++) {
            let { articleInfo } = res.data.data.detail[i];
            let { labels } = res.data.data.detail[i];
            let { userInfo, userFollow } = res.data.data.detail[i].userDetail;
            postDetailList.push({
              follow: userFollow.follow,
              articleId: articleInfo.articleId,
              uid: articleInfo.uid,
              likedNum: articleInfo.likedNum,
              like: res.data.data.detail[i].like,
              sharedNum: articleInfo.sharedNum,
              commentNum: articleInfo.commentNum,
              userHeader: userInfo.photoPath,
              userName: userInfo.nickname,
              postText: articleInfo.summary,
              postImages: [articleInfo.thumbnailPath || ""],
              tagList: labels,
              postTime: new Date(articleInfo.date).format("MM/dd"),
            });
          }
          this.posts[type].list.push(...postDetailList);
          this.page[type]++; //当前页数加一
        },
        (err) => {
          console.log(err);
        }
      );
    },
    //取消点赞
    unlikeIt(articleId) {
      setUnLike({ articleId });
    },
    // 点赞
    likeIt(articleId) {
      setLike({ articleId: articleId }).then(
        (res) => [console.log(res)],
        (err) => {
          console.log(err);
        }
      );
    },
    //关注
    noticeIt(followId) {
      setNoticeIt({ followId }).then(
        (res) => {
          this.$bus.$emit("followPerson", followId);
          for (let post of this.posts[this.activeType].list) {
            if (post.uid === followId) {
              post.follow = !post.follow;
            }
          }
        },
        (err) => {
          uni.showToast({
            title: "出了点小状况",
          });
          return;
        }
      );
    },
    //取消关注
    unNoticeIt(followId) {
      setUnnoticeIt({ followId }).then(
        (res) => {
          this.$bus.$emit("cancelFollowPerson", followId);
          for (let post of this.posts[this.activeType].list) {
            if (post.uid === followId) {
              post.follow = !post.follow;
            }
          }
        },
        (err) => {
          uni.showToast({
            title: "出了点小状况",
          });
          return;
        }
      );
    },
  },
  //上拉触底函数
  onReachBottom() {
    if (this.maxPageNum < this.page[this.activeType]) {
      this.loadStatus = "nomore";
      return;
    }
    if (!this.isLoadMore) {
      //此处判断，上锁，防止重复请求
      this.isLoadMore = true;
      this.getPost(
        this.activeType,
        [], //登陆用户的还没做好
        this.page[this.activeType],
        this.loadSize //每次拉去20条
      );
    }
    this.isLoadMore = false;
  },
  // 下拉刷新
  onPullDownRefresh() {
    this.posts[this.activeType].list = [];
    this.page[this.activeType] = 1;
    this.getPost(
      this.activeType,
      null,
      this.page[this.activeType],
      this.loadSize,
      null
    ).then(
      (res) => {
        uni.stopPullDownRefresh(); //停止当前页面下拉刷新
        uni.showToast({ title: "刷新成功", icon: "none" });
      },
      (err) => {
        uni.stopPullDownRefresh(); //停止当前页面下拉刷新
        uni.showToast({ title: "出了点小状况", icon: "none" });
      }
    );
  },
  mounted() {
    this.$bus.$on("sycnlike", (data, like) => {
      for (const item of this.posts[this.activeType].list) {
        if (item.articleId === data) {
          item.like = !item.like;
          if (like) {
            item.likedNum++;
          } else {
            item.likedNum--;
          }
          break;
        }
      }
    });
    //初次挂载页面就要加载一定的数据(关注)
    this.getPost("follow", null, this.page["follow"], this.loadSize, null);
    //初次挂载页面就要加载一定的数据(推荐)
    this.getPost(
      "recommend",
      null,
      this.page["recommend"],
      this.loadSize,
      null
    );
  },
};
</script>

<style scoped>
.scrollY {
  margin-top: 80rpx;
  padding-top: 80rpx;
  height: 94vh;
}
::-webkit-scrollbar {
  width: 0;
  height: 0;
  background-color: transparent;
}
</style>
